import React,{useEffect} from 'react';
import {useDispatch,useSelector} from 'react-redux';
import {Link} from 'react-router-dom';
import {Row,Col,ListGroup,Image,Form,Button,Card} from 'react-bootstrap';
import Message from "../components/Message";
import {addToCart,removeFromCart} from "../actions/cartActions";

const CartScreen = ({match,location,history}) =>{
	const productId=match.params.id;
	const qty=location.search.split("=")[1];
	const dispatch= useDispatch();
	useEffect(()=>{
		if(productId){
			dispatch(addToCart(productId,qty))
		}
	},[dispatch,qty,productId])

	const removeFromCartHandler = (id)=>{
		dispatch(removeFromCart(id))
	}

	const checkoutHandler=()=>{
		history.push("/login?redirect=shipping")
	}

	const {cartItems} = useSelector(state=>state.cart);

	return (
		<Row>
			<Col md={8}>
				{cartItems.length === 0? <Message>Your shopping cart is empty <Link to="/">Go Back</Link></Message> : (
					<ListGroup variant='flush'>
						{cartItems.map((item)=>(
							<ListGroup.Item key={item.product}>
								<Row>
									<Col md={2}>
										<Image src={item.image} alt={item.name} fluid rounded />
									</Col>
									<Col md={3}>
										<Link to={`/product/${item.product}`}>{item.name}</Link>
									</Col>
									<Col md={2}>{item.price}</Col>
									<Col md={3}>
										<Form.Control as='select' value={item.qty} onChange={(e)=>dispatch(addToCart(item.product,Number(e.target.value)))}>
											{[...Array(item.countInStock).keys()].map(x=>(
												<option value={x+1} key={x+1}>{x+1}</option>
											))}
										</Form.Control>
									</Col>
									<Col md={2}>
										<Button variant="light" type='button' onClick={()=>removeFromCartHandler(item.product)}>
											<i className='fas fa-trash'></i>
										</Button>
									</Col>
								</Row>
							</ListGroup.Item>
						))}
					</ListGroup>
				)}
			</Col>
			<Col md={4}>
				<Card>
					<ListGroup variant='flush'>
						<ListGroup.Item>
							<h2>Subtotal ({cartItems.reduce((acc,items)=>acc+items.qty,0)}) items</h2>
							${cartItems.reduce((acc,items)=>acc+items.qty*items.price,0).toFixed(2)}
						</ListGroup.Item>
						<ListGroup.Item>
							<Button type='button' className='btn-block' onClick={checkoutHandler}>
                        		Proceed To Checkout
							</Button>
						</ListGroup.Item>
					</ListGroup>
				</Card>
			</Col>
		</Row>
	)
}

export default CartScreen;